@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,900;1,400&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}

body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

body {
    margin: 0;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

}
.add-location {}

.add-location input {
    height: 2rem;
    width: 15rem;
    border: .01rem solid #000;
    border-radius: .3rem;
    margin-right: 1rem;
    outline: none;
    padding: 0.5rem 1rem;
}

.add-location button {
    height: 2rem;
    width: 6rem;
    border: .01rem solid #000;
    border-radius: .3rem;
    background: #000000AA;
    color: #fff;
    font-size: .9rem;
}

.add-location button:not(:disabled):hover {
    cursor: pointer;
    background: #000;
}
.add-location {
    position: absolute;
    right: 2rem;
    top: 2rem;
}


.weather-pannel {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    border: 1px solid #000;
    border-radius: 2rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .6);
    color: #fff;
    box-shadow: 0 0 1rem #000000;
}

.weather-pannel .city-name {
    display: flex;
    justify-content: center;
    align-items: center;
}

.weather-pannel .info-row {
    margin-bottom: 1rem;
}

.weather-pannel .info-row.description {
    font-size: 1.3rem;
}
